.failPage {
    height: 100vh;
    width: 100vw;
    background: rgba(255, 48, 48, 0.712);
    color: #ecf0f1;
}

.failBg {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    z-index: -5;
    opacity: 0.15;
    background: url(../../assets/fail.png);
}

.finalInner {
    padding-top: 10%;
}

.finalTitle {
    font-size: 3em;
    font-weight: bold;
}

.results {
    width: 65%;
    text-align: left;
    margin: 50px auto 0;
}

.resultsTitle {
    margin-top: 20px;
    font-size: 2em;
}

.result {
    margin: 20px 0px 0px 20px;
    font-size: 1.5em;
}

.restartButton {
    margin-top: 60px;
}

.restButton {
    border: none;
    margin-top: 100px;
    padding: 20px 30px;
    font-size: 30px;
    border-radius: 5px;
    cursor: pointer;
}

.failContacts {
    position: absolute;
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.social {
    color: #fff;
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: space-between;
    width: 450px;
}

.social-item {
    height: 70px;
    width: 70px;
    border: 5px solid #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 27px;
    transition: all .6s;
}

a {
    cursor: pointer;
    text-decoration: none;
    color: inherit;
}